'use client'

import { useRef, useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper-bundle.css'

import {
  CalendarIcon,
  ClockIcon,
  FilmIcon,
  GlobeIcon,
  StarIcon,
  MessageSquareIcon,
  BadgeDollarSignIcon,
  LinkIcon,
  TagIcon,
  ChevronLeft,
  ChevronRight
} from 'lucide-react'

export default function DetailsClient({ type, id, data, recommendations, castData, providers, reviews }) {
  const image = data.poster_path || data.profile_path
  const title = data.title || data.name
  const recRef = useRef()
  const [reviewLimit, setReviewLimit] = useState(2)

  const scrollLeft = ref => ref.current.scrollBy({ left: -400, behavior: 'smooth' })
  const scrollRight = ref => ref.current.scrollBy({ left: 400, behavior: 'smooth' })

  return (
    <div className="relative">
      {/* Fondo difuminado */}
      {data.backdrop_path && (
        <div
          className="absolute inset-0 z-0"
          style={{
            backgroundImage: `url(https://image.tmdb.org/t/p/original${data.backdrop_path})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            filter: 'blur(12px)',
            opacity: 0.25,
          }}
        />
      )}
  
      {/* Contenido principal por encima del fondo */}
      <div className="relative z-10 p-8 max-w-6xl mx-auto text-white backdrop-blur-sm bg-black/60 rounded-xl">
  
    
      {/* Cabecera */}
      <div className="flex flex-col lg:flex-row gap-8 mb-12">
        <div className="w-full lg:w-1/3 max-w-sm flex flex-col gap-4">
          {image && (
            <img src={`https://image.tmdb.org/t/p/w500${image}`} alt={title} className="rounded-lg shadow-lg w-full h-auto object-cover" />
          )}

          {/* Plataformas */}
          {providers && providers.length > 0 && (
          <div className="mt-2">
              <div className="flex flex-wrap gap-3">
              {providers.map(p => (
                  <div key={p.provider_id} className="bg-gray-800 p-2 rounded flex items-center gap-2 hover:bg-gray-700 transition-all duration-300 ease-in-out transform hover:scale-110">
                  <img src={`https://image.tmdb.org/t/p/original${p.logo_path}`} alt={p.provider_name} className="w-8 h-8 object-contain" />
                  </div>
              ))}
                {/* Icono de JustWatch */}
                <a 
                  href={`https://www.themoviedb.org/${type}/${id}/watch`} 
                  target="_blank" 
                  className="bg-gray-800 p-2 rounded flex items-center gap-2 hover:bg-gray-700 transition-all duration-300 ease-in-out transform hover:scale-110"
                  rel="noreferrer"
                >
                  <img 
                    src="https://play-lh.googleusercontent.com/Riuz226TXAawu8ZXlL7wnsjtMHkTMTDh_RSRiozAdoKe2TyGG4cLp3rPB0CxQFEUzFc" 
                    alt="JustWatch"
                    className="object-contain w-8 h-8"
                  />
                </a>

              </div>
          </div>
          )}
        </div>

        <div className="flex-1 flex flex-col gap-6">
          <div>
            <h1 className="text-4xl font-bold mb-2">{title}</h1>
            {data.tagline && (
              <p className="italic text-gray-400 mb-4">
                <TagIcon className="inline w-4 h-4 mr-1" /> {data.tagline}
              </p>
            )}
            {data.overview && (
              <p className="text-gray-300 text-base leading-relaxed">
                <MessageSquareIcon className="inline w-4 h-4 mr-1 text-gray-400" /> {data.overview}
              </p>
            )}
          </div>

          <div className="flex flex-wrap gap-2">
            {data.genres?.map((genre) => {
              // Definir un color para cada género
              const genreColors = {
                "Action": "bg-red-500",
                "Drama": "bg-blue-500",
                "Comedy": "bg-yellow-500",
                "Thriller": "bg-green-500",
                "Romance": "bg-pink-500",
                "Horror": "bg-purple-500",
                // Agregar más géneros y colores según sea necesario
              };

              // Determinar el color del género, si no existe se asigna un color predeterminado
              const genreColor = genreColors[genre.name] || "bg-gray-600";

              return (
                <span key={genre.id} className={`${genreColor} text-white px-3 py-1.5 rounded-full text-sm`}>
                  {genre.name}
                </span>
              );
            })}
          </div>

          <div className="text-sm text-gray-300 bg-gray-900 p-5 rounded-lg shadow-md space-y-1">
            {data.original_title && (
              <p>
                <FilmIcon className="inline w-4 h-4 mr-2 text-blue-400" /> <strong>Título original:</strong> {data.original_title}
              </p>
            )}
            {data.release_date && (
              <p>
                <CalendarIcon className="inline w-4 h-4 mr-2 text-green-400" /> <strong>Estreno:</strong> {data.release_date}
              </p>
            )}
            {data.runtime && (
              <p>
                <ClockIcon className="inline w-4 h-4 mr-2 text-yellow-400" /> <strong>Duración:</strong> {data.runtime} min
              </p>
            )}
            {data.original_language && (
              <p>
                <GlobeIcon className="inline w-4 h-4 mr-2 text-purple-400" /> <strong>Idioma:</strong> {data.original_language}
              </p>
            )}
            {data.vote_average && (
              <p>
                <StarIcon className="inline w-4 h-4 mr-2 text-yellow-300" /> <strong>Nota media:</strong> {data.vote_average.toFixed(1)}
              </p>
            )}
            {data.vote_count && (
              <p>
                <StarIcon className="inline w-4 h-4 mr-2 text-yellow-300" /> <strong>Votos:</strong> {data.vote_count}
              </p>
            )}
            {type === 'tv' && (
              <>
                {data.first_air_date && (
                  <p>
                    <CalendarIcon className="inline w-4 h-4 mr-2 text-green-400" /> <strong>Primera emisión:</strong> {data.first_air_date}
                  </p>
                )}
                {data.last_air_date && (
                  <p>
                    <CalendarIcon className="inline w-4 h-4 mr-2 text-red-400" /> <strong>Última emisión:</strong> {data.last_air_date}
                  </p>
                )}
                {data.number_of_seasons && (
                  <p>
                    <FilmIcon className="inline w-4 h-4 mr-2 text-blue-300" /> <strong>Temporadas:</strong> {data.number_of_seasons}
                  </p>
                )}
                {data.number_of_episodes && (
                  <p>
                    <FilmIcon className="inline w-4 h-4 mr-2 text-blue-300" /> <strong>Episodios:</strong> {data.number_of_episodes}
                  </p>
                )}
                {data.episode_run_time?.[0] && (
                  <p>
                    <ClockIcon className="inline w-4 h-4 mr-2 text-yellow-400" /> <strong>Duración por episodio:</strong> {data.episode_run_time[0]} min
                  </p>
                )}
                {data.status && (
                  <p>
                    <StarIcon className="inline w-4 h-4 mr-2 text-gray-300" /> <strong>Estado:</strong> {data.status}
                  </p>
                )}
              </>
            )}
            {data.budget > 0 && (
              <p>
                <BadgeDollarSignIcon className="inline w-4 h-4 mr-2 text-green-500" /> <strong>Presupuesto:</strong> ${data.budget.toLocaleString()}
              </p>
            )}
            {data.revenue > 0 && (
              <p>
                <BadgeDollarSignIcon className="inline w-4 h-4 mr-2 text-green-500" /> <strong>Recaudación:</strong> ${data.revenue.toLocaleString()}
              </p>
            )}
            {data.homepage && (
              <p>
                <a
                  href={data.homepage}
                  target="_blank"
                  className="text-blue-400 hover:underline flex items-center"
                  rel="noreferrer"
                >
                  <LinkIcon className="inline w-4 h-4 mr-2 text-blue-300" />
                  <strong>Sitio web</strong>
                </a>
              </p>
            )}
            {data.imdb_id && (
              <p>
                <a
                  href={`https://www.imdb.com/title/${data.imdb_id}`}
                  target="_blank"
                  className="text-yellow-400 hover:underline flex items-center"
                  rel="noreferrer"
                >
                  <LinkIcon className="inline w-4 h-4 mr-2 text-yellow-300" />
                  <strong>Enlace IMDb</strong>
                </a>
              </p>
            )}
          </div>
        </div>
      </div>

      {/* Carrusel de Reparto */}
      {castData && castData.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Reparto principal</h2>
          <Swiper
            spaceBetween={20}
            slidesPerView={6} // Ajustado para mostrar 6 actores
            breakpoints={{
              640: {
                slidesPerView: 1,
                spaceBetween: 10,
              },
              768: {
                slidesPerView: 2,
                spaceBetween: 10,
              },
              1024: {
                slidesPerView: 6, // Mostrar 6 en pantallas grandes
                spaceBetween: 20,
              },
            }}
          >
            {castData.slice(0, 20).map(actor => (
              <SwiperSlide key={actor.id} className="flex-shrink-0 text-center">
                <div className="relative">
                  {/* Enlace para redirigir al detalle del actor */}
                  <a href={`/details/person/${actor.id}`} className="block">
                    <img
                      src={`https://image.tmdb.org/t/p/w185${actor.profile_path}`}
                      alt={actor.name}
                      className="w-full h-60 object-cover rounded-lg"
                    />
                    <div className="absolute bottom-0 left-0 right-0 bg-black bg-opacity-50 text-white p-2">
                      <p className="font-semibold text-sm">{actor.name}</p>
                    </div>
                  </a>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      )}

      {/* Críticas */}
      {reviews && reviews.length > 0 && (
        <div className="mt-12">
            <h2 className="text-2xl font-bold text-white mb-4">Críticas de usuarios</h2>
            <div className="grid md:grid-cols-2 gap-6">
            {reviews.slice(0, reviewLimit).map(review => (
                <div key={review.id} className="bg-gray-800 p-4 rounded-lg">
                <p className="text-white font-semibold">{review.author}</p>
                <p className="text-gray-400 text-sm mb-2">{new Date(review.created_at).toLocaleDateString()}</p>
                <p className="text-gray-300 whitespace-pre-wrap text-sm">{review.content.slice(0, 300)}...</p>
                <a href={`https://www.themoviedb.org/review/${review.id}`} target="_blank" className="text-blue-400 underline text-sm">Leer más →</a>
                </div>
            ))}
            </div>
            {reviewLimit < reviews.length && (
            <div className="text-center mt-4">
                <button onClick={() => setReviewLimit(reviewLimit + 2)} className="px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded text-white">Ver más críticas</button>
            </div>
            )}
        </div>
      )}

      {/* Carrusel de Recomendaciones */}
      {recommendations && recommendations.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Recomendaciones</h2>
          <Swiper
            spaceBetween={20}
            slidesPerView={6} // Ajustado para mostrar 6 recomendaciones
            breakpoints={{
              640: {
                slidesPerView: 1,
                spaceBetween: 10,
              },
              768: {
                slidesPerView: 2,
                spaceBetween: 10,
              },
              1024: {
                slidesPerView: 6, // Mostrar 6 en pantallas grandes
                spaceBetween: 20,
              },
            }}
          >
            {recommendations.slice(0, 20).map(rec => (
              <SwiperSlide key={rec.id} className="flex-shrink-0 text-center">
                <div className="relative">
                  {/* Enlace para redirigir al detalle de la recomendación */}
                  <a href={`/details/${rec.media_type || type}/${rec.id}`} className="block">
                    <img
                      src={`https://image.tmdb.org/t/p/w300${rec.poster_path}`}
                      alt={rec.title || rec.name}
                      className="w-full h-full object-cover rounded-lg"
                    />
                    <div className="text-white p-2">
                      <p className="font-semibold text-sm">{rec.title || rec.name}</p>
                      {rec.vote_average && (
                        <p className="text-xs text-yellow-400">⭐ {rec.vote_average.toFixed(1)}</p>
                      )}
                    </div>
                  </a>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      )}
    </div>
    </div>
  )
}
